html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: linear-gradient(90deg, #B363C4, #6157D1);
  background-color: #B363C4;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  padding-right: 2rem;
  padding-left: 2rem;
  margin-right: auto;
  margin-left: auto;
}

.todo .container {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  color: #FEF7FF;
  padding: 0 20px;
  padding-bottom: 50px;
  font-size: 3rem;
}

.todo h2 {
  margin-bottom: 3rem;
  font-size: 4rem;
  font-weight: 500;
  margin-top: 30px;
}

.input-icon {
  position: relative;
  display: flex;
  width: 350px;

  form {
    display: block;
    width: 100%;
  }
}

.input-icon img {
  position: absolute;
  right: .4rem;
  color: #B363C4;
  cursor: pointer;
  height: 2.5rem;
  top: 50%;
  transform: translateY(-50%);
}

.input-icon input {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 2.5rem;
  outline: none;
  border: 2px solid white;
  padding-left: 15px;
  font-size: 1.6rem;
}

.the-list {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  width: 100%;
  max-width: 700px;
}

.task {
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
  background-color: white;
  width: 100%;
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: black;
  transition: .5s;
  padding: .4rem .5rem;
  height: auto;

  .arrow {
    width: 1.5rem;
    height: 1.5rem;
    border-right: 3px solid gray;
    border-bottom: 3px solid gray;
    transform: rotate(45deg);
    border-radius: .25rem;
    justify-self: flex-end;
    transition: .2s;
  }

  .active {
    transform: rotate(-135deg);
  }

  .row-2 {
    height: auto;
    overflow: hidden;
  }

  .row-1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    [alt=trash] {
      width: 2.1rem;
      height: 2.1rem;
      padding: .4rem;
    }

    .icons {
      display: flex;
      align-items: center;
    }
  }
}

@keyframes done {
  50% {
    transform: rotate(360deg);
  }

  100% {
    min-width: 40%;
  }
}

.task h3 {
  font-weight: 500;
}

.done {
  opacity: .6;
  transform: scale(.9);
}

.task img:nth-child(1) {
  color: white;
  height: 2.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.task img:nth-child(2) {
  margin-left: 10px;
  color: white;
  height: 1.4rem;
  padding: .5rem;
  border-radius: 7px;
  background-color: #EB4522;
  cursor: pointer;
}

.done h3 {
  text-decoration: line-through;
}

.delet {
  transition: .5s;
  transform: scale(0);
}

@media (max-width:600px) {
  html {
    font-size: 12px;
  }

  .input-icon {
    width: 90%;
  }

  .the-list .task {
    width: 70%;
  }
}