html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#B363C4), to(#6157D1));
  background: linear-gradient(90deg, #B363C4, #6157D1);
  background-color: #B363C4;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  padding-right: 2rem;
  padding-left: 2rem;
  margin-right: auto;
  margin-left: auto;
}

.todo .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #FEF7FF;
  padding: 0 20px;
  padding-bottom: 50px;
  font-size: 3rem;
}

.todo h2 {
  margin-bottom: 3rem;
  font-size: 4rem;
  font-weight: 500;
  margin-top: 30px;
}

.input-icon {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 350px;
}

.input-icon form {
  display: block;
  width: 100%;
}

.input-icon img {
  position: absolute;
  right: .4rem;
  color: #B363C4;
  cursor: pointer;
  height: 2.5rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.input-icon input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
  height: 2.5rem;
  outline: none;
  border: 2px solid white;
  padding-left: 15px;
  font-size: 1.6rem;
}

.the-list {
  margin-top: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  width: 100%;
  max-width: 700px;
}

.task {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
  width: 100%;
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: black;
  -webkit-transition: .5s;
  transition: .5s;
  padding: .4rem .5rem;
  height: auto;
}

.task .arrow {
  width: 1.5rem;
  height: 1.5rem;
  border-right: 3px solid gray;
  border-bottom: 3px solid gray;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-radius: .25rem;
  justify-self: flex-end;
  -webkit-transition: .2s;
  transition: .2s;
}

.task .active {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
}

.task .row-2 {
  height: auto;
  overflow: hidden;
}

.task .row-1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}

.task .row-1 [alt=trash] {
  width: 2.1rem;
  height: 2.1rem;
  padding: .4rem;
}

.task .row-1 .icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@-webkit-keyframes done {
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    min-width: 40%;
  }
}

@keyframes done {
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    min-width: 40%;
  }
}

.task h3 {
  font-weight: 500;
}

.done {
  opacity: .6;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.task img:nth-child(1) {
  color: white;
  height: 2.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.task img:nth-child(2) {
  margin-left: 10px;
  color: white;
  height: 1.4rem;
  padding: .5rem;
  border-radius: 7px;
  background-color: #EB4522;
  cursor: pointer;
}

.done h3 {
  text-decoration: line-through;
}

.delet {
  -webkit-transition: .5s;
  transition: .5s;
  -webkit-transform: scale(0);
          transform: scale(0);
}

@media (max-width: 600px) {
  html {
    font-size: 12px;
  }
  .input-icon {
    width: 90%;
  }
  .the-list .task {
    width: 70%;
  }
}
